import { useStaticQuery, graphql } from "gatsby";

import {
  Header,
  DropDown,
  Job,
  isValidJob,
  Skills,
  isValidSkills,
  Education,
  Layout,
} from "components";

const hrBreak = {
  height: "3px",
  backgroundColor: "black",
};

const Index = () => {
  const { skills, work } = useStaticQuery<Queries.IndexQuery>(query);
  return (
    <Layout>
      <Header />
      <h2>What I&apos;m Looking For</h2>
      <ul>
        <li>
          A dope role working as a <b>Senior Front End / Full Stack</b>
        </li>
        <li>A company that cares about creating a quality product / service</li>
        <li>
          Work culture that cares about people, but doesn't claim to be a{" "}
          <i>family</i>
        </li>
        <li>Transparent leadershipt open to feedback</li>
        <li>Competitive base salary - shares and bonuses are just bonuses</li>
      </ul>
      <hr />
      <Education
        school="Purdue University"
        degrees={[
          {
            degree: "M.S.",
            program: "Computer Graphics Technology - User Experience Design",
            year: "2016",
          },
          {
            degree: "B.S.",
            program: "Computer Graphics Technology - Web Development",
            year: "2014",
          },
        ]}
      />
      <h2 style={{ marginBottom: "5px" }}>Work</h2>
      <hr style={hrBreak} />
      {work.edges.map(
        ({ node }) =>
          isValidJob(node) && (
            <Job key={`${node.employer}-${node.title}`} {...node} />
          )
      )}

      <h2 style={{ marginBottom: "5px" }}>Skills</h2>
      <hr style={hrBreak} />
      {skills.edges.map(
        ({ node: el }) =>
          el.title &&
          isValidSkills(el) && (
            <DropDown title={el.title} key={el.title}>
              <Skills skills={el.skills} />
            </DropDown>
          )
      )}
    </Layout>
  );
};

export const query = graphql`
  query Index {
    skills: allSkillsJson {
      edges {
        node {
          title
          skills
        }
      }
    }
    work: allWorkJson {
      edges {
        node {
          employer
          start
          end
          location
          title
          duties
        }
      }
    }
  }
`;

export default Index;
